.card {
    border-radius: 10px;
    height: 150px;
    margin: 1rem;
    position: relative;
    width: 150px;
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    
  }
  .card > .img-container > img {
    width: 100%;
    height:100%;
    cursor: grab;
  }

  .img-thumbnail:hover {
    opacity: 0.3;
    }
.img-container:hover{
    transform: scale(1.1);
}

    